h1, h3 {
  margin: 15px 0;
}

main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-direction: column;
      flex-direction: column;
}

 
.event-date{
  margin: 0 15px 0 10px;
}

.container {
  padding-bottom: 50px;
}

.title-bar {

}
.events-list-year-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

div.events-list-year-container >div:nth-child(odd) {
  background-color: rgb(219, 219, 219);
}

.events-list-year-container > p {
  margin: 0;
}

div.indexed-event{
  padding: 10px 5px;
  border: .25px solid rgb(205, 205, 205);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;  
  /*flex-wrap: wrap;*/
  -ms-flex-align: space-between;
      align-items: space-between;
}

span.event-date{
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
  min-width: 140px
}

a.event-name {
  margin: 0 0 0 10px;
  color: rgb(0,0,0);
} 





